.card {
    position: relative;
    width: 21.25rem;
    height: 21.25rem;
    border-radius: 0.625rem;
 

    @media (min-width: 767.99px) and (max-width :1024px) {
        max-width: 16.25rem;
        max-height: 16.25rem;
    }

    @media (min-width: 424.99px) and (max-width: 767.99px) {
        width: 100%;
    }

    &__picture {
       @include wh_100;
        border-radius: 10px;
        object-fit: cover;
    }

    &__title {
        position: absolute;
        margin: 0;
        left: 1.25rem;
        right: 1.25rem;
        bottom: 1.25rem;
        font-size: 1.125rem;
        font-weight: 500;
        text-align: left;
        color: white;
    }
}


