.gallery {
    width: 90%;
    margin: auto;
    @include flex-center;
    flex-wrap: wrap;
    align-items: center;
    flex: 1 0 auto;
    gap: 3.125rem;
    padding: 3.125rem;
    margin-top: 2.5rem;
    margin-bottom: 2.5rem;
    background-color: #f6f6f6;
    border-radius: 20px;
}

@media (min-width: 1024px) and (max-width :1279.99px) {}

@media (min-width: 768px) and (max-width :1023.99px) {}

@media (min-width: 540px) and (max-width : 767.99px) {}


@media (min-width: 350px) and (max-width: 539.99px) {
    .gallery {
        padding: inherit;
        gap: 1rem;
    }
    }
